import React from 'react';
import JSZip from 'jszip';
import { saveAs } from 'file-saver';

function ConversionButtons({ uploadedImages, onConvert }) {
    const handleConvert = (format) => {
        const convertedImages = uploadedImages.map(image => {
            // Simulate conversion by changing the format and maintaining the original name
            const fileNameWithoutExt = image.name.split('.').slice(0, -1).join('.');
            const newFileName = `${fileNameWithoutExt}.${format}`;
            return { ...image, format, converted: true, newFileName };
        });
        onConvert(convertedImages);
    };

    const downloadAsZip = async () => {
        const zip = new JSZip();
        await Promise.all(uploadedImages.map(async (file) => {
            const response = await fetch(file.preview);
            const blob = await response.blob();
            zip.file(file.newFileName || file.name, blob);
        }));

        zip.generateAsync({ type: 'blob' }).then(content => {
            saveAs(content, 'Snap_Convert.zip');
        });
    };

    return (
        <>
            <div className='snap_btn_group'>
                <div className='row row-cols-lg-4'>
                    <div className='col'>
                        <button className='btn btn-primary' onClick={() => handleConvert('jpeg')}>Convert to JPEG</button>
                    </div>
                    <div className='col'>
                        <button className='btn btn-warning' onClick={() => handleConvert('png')}>Convert to PNG</button>
                    </div>
                    <div className='col'>
                        <button className='btn btn-info' onClick={() => handleConvert('webp')}>Convert to WEBP</button>
                    </div>
                    <div className='col'>
                        <button className='btn btn-dark mb-0' onClick={() => handleConvert('avif')}>Convert to AVIF</button>
                    </div>
                </div>
            </div>
            <div className='snap_btn_bottom'>
                <button className='btn btn-success mb-0' onClick={downloadAsZip}>Download All</button>
            </div>
        </>
    );
}

export default ConversionButtons;
