import React from 'react';

function ConvertedImages({ convertedImages }) {
    return (
        <div className='snap_converted_wrap'>
            <div className='section_titel'>
                <h2>Converted Images</h2>
            </div>
            <div className='row row-cols-lg-5 row-cols-md-3 row-cols-sm-2 row-cols-1'>
                {convertedImages.map((image, index) => (
                    <div className='col'>
                        <div key={index} className='snap_converted_item'>
                            <img className='snap_converted_img' src={image.preview} alt={image.newFileName || `converted_image_${index}.${image.format || 'jpeg'}`} />
                            <a className='btn btn_icon' href={image.preview} download={image.newFileName || `converted_image_${index}.${image.format || 'jpeg'}`}>
                                <span className='fa-solid fa-floppy-disk'></span>
                            </a>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
}
export default ConvertedImages;
