// App.js
import React, { useState } from 'react';
import MultiUpload from './multiUpload';
import ConversionButtons from './conversionButtons';
import ConvertedImages from './convertedImages';
import brand from './brand_logo.png';
import './app.css';

function App() {
  const [uploadedImages, setUploadedImages] = useState([]);
  const [convertedImages, setConvertedImages] = useState([]);

  const handleConversion = (newImages) => {
    setConvertedImages(newImages);
  };

  return (
    <section className='app_body'>
      <div className='container'>
        <div className='row'>
          <div className='col-12'>
            <div className='snapapp_wrap text-center'>
              <div className='brand_logo'>
                <img src={brand} alt='Snapconvert' />
              </div>
              <div className='section_titel'>
                <h1>Welcome to  Snapconvert</h1>
              </div>
              <div className='drag_wrap'>
                <MultiUpload onUpload={setUploadedImages} />
                <p className='count_span'><span>{uploadedImages.length}</span> items...</p>
              </div>
              <div className='snap_btns'>
                <ConversionButtons uploadedImages={uploadedImages} convertedImages={convertedImages} onConvert={handleConversion} />
              </div>
              <div className='snap_converted'>
                <ConvertedImages convertedImages={convertedImages} />
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default App;
